.button {
  margin: 0;
  overflow: visible;
  display: inline-block;
  padding: 10px 30px;
  outline: none;
  vertical-align: middle;
  font-size: 0.875rem;
  text-align: center;
  text-transform: uppercase;
  -webkit-transition: 0.1s ease-in-out;
  -o-transition: 0.1s ease-in-out;
  transition: 0.1s ease-in-out;
  -webkit-transition-property: color, background-color, border-color;
  -o-transition-property: color, background-color, border-color;
  transition-property: color, background-color, border-color;
  cursor: pointer; }

.success {
  background: #5ba50a;
  color: #fff;
  border: 1px solid transparent; }
  .success:hover {
    text-decoration: none;
    background: #76d50d; }

.primary {
  background: #477dfc;
  color: #fff;
  border: 1px solid transparent; }
  .primary:hover {
    text-decoration: none;
    background: #79a0fd; }

.secondary {
  background: #aaa;
  color: #000;
  border: 1px solid transparent; }
  .secondary:hover {
    text-decoration: none;
    background: #c4c4c4; }

.warning {
  background: #f18a00;
  color: #fff;
  border: 1px solid transparent; }
  .warning:hover {
    text-decoration: none;
    background: #ffa225; }

.danger {
  background: #f0506e;
  color: #fff;
  border: 1px solid transparent; }
  .danger:hover {
    text-decoration: none;
    background: #f47f95; }
