.circular-progress-bar {
  width: 100%;
  max-width: 250px;
  vertical-align: middle; }

.circular-progress-bar .green, .circular-progress-bar .red, .circular-progress-bar .amber {
  stroke-linecap: butt;
  -webkit-transition: stroke-dashoffset 0.5s ease 0s;
  -o-transition: stroke-dashoffset 0.5s ease 0s;
  transition: stroke-dashoffset 0.5s ease 0s; }

.green {
  stroke: #92d400; }

.red {
  stroke: #f25544; }

.amber {
  stroke: #ffbf00; }

.circular-progress-bar .circular-progress-bar-trail {
  stroke: #d6d6d6;
  stroke-linecap: round; }

.circular-progress-bar .circular-progress-bar-text {
  fill: #666;
  font-size: 20px;
  font-family: 'BrandonText-Regular', Arial, Helvetica, sans-serif;
  dominant-baseline: middle;
  text-anchor: middle; }

.circular-progress-bar .circular-progress-bar-background {
  fill: #d6d6d6; }

.circular-progress-bar.circular-progress-bar-inverted .circular-progress-bar-background {
  fill: #3e98c7; }

.circular-progress-bar.circular-progress-bar-inverted .circular-progress-bar-text {
  fill: #fff; }

.circular-progress-bar.circular-progress-bar-inverted .circular-progress-bar-path {
  stroke: #fff; }

.circular-progress-bar.circular-progress-bar-inverted .circular-progress-bar-trail {
  stroke: transparent; }
