/* http://meyerweb.com/eric/tools/css/reset/
   v2.0 | 20110126
   License: none (public domain)
*/
html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  vertical-align: baseline; }

/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block; }

body {
  line-height: 1; }

ol,
ul {
  list-style: none; }

blockquote,
q {
  quotes: none; }

blockquote::before,
blockquote::after,
q::before,
q::after {
  content: none; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

@font-face {
  font-family: 'BrandonText';
  font-style: normal;
  font-weight: normal;
  src: url("../assets/fonts/BrandonText-Regular.otf") format("opentype"); }

@font-face {
  font-family: 'BrandonText';
  font-style: italic;
  font-weight: normal;
  src: url("../assets/fonts/BrandonText-RegularItalic.otf") format("opentype"); }

@font-face {
  font-family: 'BrandonText';
  font-style: normal;
  font-weight: bold;
  src: url("../assets/fonts/BrandonText-Bold.otf") format("opentype"); }

@font-face {
  font-family: 'BrandonText';
  font-style: italic;
  font-weight: bold;
  src: url("../assets/fonts/BrandonText-BoldItalic.otf") format("opentype"); }

body {
  font-family: 'BrandonText', Arial, Helvetica, sans-serif !important;
  background-color: #f8f9fc; }

h4 {
  font-size: 1.5rem;
  font-weight: bold; }
